/*class for styling */
.list{
    color: white;
    width: 300px;
    background-color: cadetblue;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;

}
.list p{ /*paragraph tabs*/
    padding: 10px; /*adjusts where font is in the tab*/
    position: relative;
}
.list p span{
    position: absolute;
    right: 10px; /*trash icon goes all the way to right of screen*/
    cursor: pointer;
}
.faicons{
    margin: 0 5px;
    font-size: 15px;
}
.list p input{
    background-color: transparent;
    border: 0;
    color: white;
    font-size: 18px;

}
.list p input:focus{
    outline: none;
}