body{
  background-color: midnightblue;
}
.App{
  background-color: lightblue;
  min-height: 500px;
  width: 400px;
  margin: 40px auto; /*top and bottom is 40px and auto centered*/
}
#to-do-form input{
  background-color: whitesmoke;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px; 
  margin: 20px;
  font-size: 15px;
  border-radius: 10px; /*curved edges*/
}
#to-do-form input::placeholder{
  color: rgb(0, 0, 0); /*font color*/
}
#to-do-form input:focus, #to-do-form button:focus{
  outline: none; /*when you select this, no selection outline*/
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: teal;
  cursor: pointer; /*when hovering over button, turn to hand symbol*/
  font-weight: bolder;
  color: white;
}